import Vue from 'vue'
import Router from 'vue-router'
import Bicycle from '@/views/bicycle'
import Back from '@/views/back'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'bicycle',
      component: Bicycle
    },
    {
      path:'/back',
      name: 'back',
      component: Back
    }
  ]
})



