<template>
    <div class="bicycle-container">
        <div class="header"><h1>公益单车小程序&nbsp;|&nbsp;页面展示</h1></div>
        <div class="pic-box">
            <ul>
                <li v-for="(item,index) in bicycle" :key="index">
                    <img :src="item.url"/>
                    <h4>{{item.name}}</h4>
                </li>
            </ul>
        </div>
    </div>    
</template>

<script>
export default {
    data() {
        return {
            bicycle: [
                {
                    url: '../../../static/image/bicycle/6.jpg',
                    name: '首页',
                },
                {
                    url: '../../../static/image/bicycle/5.jpg',
                    name: '拍照及文字上传',
                },
                {
                    url: '../../../static/image/bicycle/4.jpg',
                    name: '上传成功页面',
                },
                {
                    url: '../../../static/image/bicycle/1.jpg',
                    name: '个人中心页',
                },
                {
                    url: '../../../static/image/bicycle/3.jpg',
                    name: '记录类型选择',
                },
                {
                    url: '../../../static/image/bicycle/2.jpg',
                    name: '帮助记录页面',
                },
            ],
        }
    },
}
</script>

<style scoped>
ul,li {
    list-style: none;
}

.pic-box ul {
    margin: 0 100px;
    margin-top: 50px;
}

.pic-box ul li {
    display: inline-block;
    padding: 0 60px;
    margin-bottom: 50px;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

.pic-box ul li:hover {
    -webkit-transform: scale(1.4);
    -moz-transform: scale(1.4);
    transform: scale(1.4);
    -o-transform: scale(1.4);
}

.pic-box ul li img{
    width: 220px;
    height: 370px;
    cursor: pointer;
    border: 2px solid white;
    border-radius: 10px;
    box-shadow: 0 0 10px white;
}
</style>




